import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/home.vue'
import Cart from '../views/cart/cart.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		redirect: '/home'
	},
	{
		name: 'home',
		path: '/home',
		component: Home,
		meta: {
			title: '首页'
		}
	},
	{
		name: 'login',
		path: '/login',
		component: () => import('@/views/login/login'),
		meta: {
			title: '登录',
			isleftarrow: true,
		}
	},
	{
		name: 'cart',
		path: '/cart',
		component: Cart,
		meta: {
			title: '购物车',
			isleftarrow: true,
			isCart: true,
		}
	},
	{
		name: 'order-submit',
		path: '/order-submit',
		component: () => import('@/views/order/order-submit'),
		meta: {
			title: '填写订单',
			isleftarrow: true,
		}
	},
	{
		name: 'user',
		path: '/user',
		component: () => import('@/views/user/user'),
		meta: {
			title: '我的'
		}
	},
	{
		name: 'order-list',
		path: '/order-list',
		component: () => import('@/views/order/order-list'),
		meta: {
			title: '所有订单'
		}
	},
	{
		name: 'order-detail',
		path: '/order-detail',
		component: () => import('@/views/order/order-detail'),
		meta: {
			title: '订单详情',
			isleftarrow: true,
		}
	},
	{
		name: 'setting',
		path: '/setting',
		component: () => import('@/views/user/setting'),
		meta: {
			title: '编辑资料'
		}
	},
	{
		name: 'cashLog',
		path: '/cashLog',
		component: () => import('@/views/user/cashLog'),
		meta: {
			title: '资金明细',
			isleftarrow: true,
		}
	},
	{
		name: 'favgoods',
		path: '/favgoods',
		component: () => import('@/views/user/favgoods'),
		meta: {
			title: '我的收藏',
			isleftarrow: true,
		}
	},
	{
		name: 'coupon',
		path: '/coupon',
		component: () => import('@/views/user/coupon'),
		meta: {
			title: '优惠券',
			isleftarrow: true,
		}
	},
	{
		name: 'address-list',
		path: '/address-list',
		component: () => import('@/views/user/address-list'),
		meta: {
			title: '收货地址',
			isleftarrow: true,
		}
	},
	{
		name: 'new-address',
		path: '/new-address',
		component: () => import('@/views/user/new-address'),
		meta: {
			title: '新增地址',
			isleftarrow: true,
		}
	},
	{
		name: 'goods-detail',
		path: '/goods-detail',
		component: () => import('@/views/goods_detail/goods_detail'),
		meta: {
			title: '商品详情'
		}
	}
]

const router = new VueRouter({
	mode: 'history',
	routes,
	scrollBehavior(to, from, savedPosition) {
		console.log('savedPosition', savedPosition);
		if (savedPosition) {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve({
						x: savedPosition.x,
						y: savedPosition.y
					})
				}, 500);
			});
		} else {
			return new Promise((resolve, reject) => {
				setTimeout(() => {
					resolve({
						x: 0,
						y: 0
					})
				}, 500);
			});
		}
	}
})

//添加标题
router.beforeEach((to, from, next) => {
	const title = to.meta && to.meta.title;
	if (title) {
		document.title = title;
	}
	next();
});
export default router
