<template>
	<div class="container">
		<!-- 轮播图开始 -->
		<van-swipe class="goods-swipe" :autoplay="3000" indicator-color="#ff6700">
			<van-swipe-item v-for="item in banner" :key="item.id">
				<!-- <img :src="item.picUrl" :alt="item.title"> -->
				<img v-lazy="item.picUrl">
			</van-swipe-item>
		</van-swipe>
		<!-- 轮播图结束 -->

		<div class="main">
			<!-- 砍价 -->
			<div class="section-header common-h2" v-if="listKanjia.length">
				<span class="common-h2-title">疯狂砍价</span>
			</div>
			<div class="list">
				<van-col span="12" v-for="(item,index) in listKanjia" :key="item.id">
					<div class="list-item" @click="toGoodDetail(item.id)">
						<img class="list-item-img" :src="item.pic" :alt="item.name">
						<div class="list-item-hd van-ellipsis">{{item.name}}</div>
						<div class="list-item-bd">
							<span class="ui-c-red">￥{{item.kanjiaPrice}}</span>
							<span class="ui-c-dark ui-text-deleted">￥{{item.originalPrice}}</span>
						</div>
					</div>
				</van-col>
			</div> 
			

			<!-- 全民拼团 -->
			<!-- <div class="section-header common-h2" v-if="listPintuan.length">
				<span class="common-h2-title">全民拼团</span>
			</div>
			<div class="list">
				<van-col span="12" v-for="item in listKanjia" :key="item.id">
					<div class="list-item" @click="toGoodDetail(item.id)">
						<img class="list-item-img" :src="item.pic" :alt="item.name">
						<div class="list-item-hd van-ellipsis">{{item.name}}</div>
						<div class="list-item-ft">
							<div class="ui-c-red" v-if="pintuanSets.length"><span class="fz12">{{pintuanSets[index].numberPersion}}人团</span>
								￥{{item.pingtuanPrice}}</div>
							<div class="ui-c-gray-dark fz12">单买价￥{{item.minPrice}}</div>
						</div>
					</div>
				</van-col>
			</div>
			-->

			<!-- 普通商品 -->
			<div class="section-header common-h2" v-if="listKanjia.length">
				<span class="common-h2-title">商品列表</span>
			</div>
			<div class="list">
				<van-row>
					<van-col span="12" v-for="item in list" :key="item.id">
						<div class="list-item" @click="toGoodDetail(item.id)">
							<div class="list-item-tag" v-if="item.recommendStatus">推荐</div>
							<img class="list-item-img" :src="item.pic" :alt="item.name">
							<div class="list-item-hd van-ellipsis">{{item.name}}</div>
							<div class="list-item-bd"><span class="ui-c-red">￥{{item.minPrice}}</span>
								<!-- <span class="ui-c-dark ui-text-deleted">￥{{item.originalPrice}}</span> -->
							</div>
						</div>
					</van-col>
				</van-row>
			</div>
		</div>

		<!-- 底部栏开始 -->
		<van-tabbar route v-model="active" active-color="#ff6700">
			<!-- <van-tabbar-item name="home" icon="home-o">首页</van-tabbar-item>
			<van-tabbar-item name="cart" icon="shopping-cart-o">购物车</van-tabbar-item>
			<van-tabbar-item name="user" icon="user-o">我的</van-tabbar-item> -->

			<van-tabbar-item replace to="/home" icon="home-o">
				首页
			</van-tabbar-item>
			<van-tabbar-item replace to="/cart" icon="cart-o">
				购物车
			</van-tabbar-item>
			<van-tabbar-item replace to="/user" icon="manager-o">
				我的
			</van-tabbar-item>
		</van-tabbar>
		<!-- 底部栏结束s -->
	</div>
</template>

<script>
	
	export default {
		data() {
			return {
				active: 0,
				banner: [],
				list: [],
				listKanjia: [],
				listPintuan: [],
				pintuanSets: [],
				goodsMap: {}
			};
		},
		created() {
			this.getList();
			this.getBanners();
			this.getGoodsKjList();
		},
		methods: {
			getBanners() {
				this.$request.get('/banner/list', {
					type: 'banner'
				}).then(res => {
					
					this.banner = res.data;
					console.log(this.banner);
				})
			},
			getList() {
				this.$request.post('/shop/goods/list').then(res => {
					console.log(res)
					this.list = res.data || []
				})
			},
			getGoodsKjList() {
				this.$request.post('/shop/goods/list', {
					kanjia: true
				}).then(res => {
					if (res.code !== 0) {
						return;
					}
					this.listKanjia = res.data || []
					this.listKanjia = this.listKanjia.filter(item => item.kanjia && item.kanjiaPrice);
					// console.log(this.listKanjia);
				})
			},
			getGoodsPtList() {
				this.$request.post('/shop/goods/list').then(res => {
					if (res.code !== 0) {
						return
					}
					this.listPintuan = res.data || []
					const goodsIdArr = this.listPintuan.map(v => v.id).join()
					this.$request.get('/shop/goods/pingtuan/sets', {
						goodsId: goodsIdArr
					}).then(res => {
						this.pintuanSets = res.data
					})
				})
			},
			toGoodDetail(id) {
				this.$router.push({
					path:'/goods-detail',
					query:{
						id
					}
				})
			}
		}
	};
</script>

<style lang="less" scoped>
	.van-tabbar {
		bottom: -1px !important;
		background-color: rgb(254, 254, 254);
		border-top: 1px solid rgba(0, 0, 0, 0.1);
		// box-shadow: 0px 0px 2px rgba(0,0,0,0.1);
	}

	.goods-swipe .van-swipe-item {
		color: #fff;
		font-size: 20px;
		line-height: 150px;
		text-align: center;
		background-color: #39a9ed;
	}

	.goods-swipe,
	.goods-swipe img {
		width: 100%;
		height: 200px;
	}

	.main {
		margin-bottom: 12vw;
	}

	.common-h2 {
		background: none;
		// background-color: #ff6400;
	}

	.list {
		padding-left: 5px;
		padding-right: 5px;
		height: auto;
		display: flex;
		flex-wrap: wrap;

		.van-col {
			box-sizing: border-box;
			padding-left: 5px;
			padding-right: 5px;
			margin-bottom: 10px;
		}

		&-item {
			position: relative;
			text-align: left;
			overflow: hidden;
			background: #fff;
			height: 250px;

			// border-radius: 8px;
			// box-shadow: 0px 2px 4px 3px rgba(243, 243, 243, 0.5);
			&-tag {
				position: absolute;
				top: 15px;
				left: 0;
				background-color: #f44;
				display: inline-block;
				padding: 0.2em 0.5em;
				color: #fff;
				font-size: 10px;
				line-height: normal;
				border-radius: 0 0.8em 0.8em 0;
				padding-right: 0.6em;
			}

			&-img {
				display: block;
				width: 100%;
				height: 180px;
			}

			&-hd {
				padding: 10px 10px 0;
				font-size: 14px;
				color: #333;
				font-size: 14px;
				// margin-bottom: 5px;
			}

			&-ft {
				padding: 10px 10px;
				display: block;
				font-size: 14px;
			}

			&-bd {
				padding: 10px 10px;
				display: block;
				font-size: 14px;
				display: flex;
				justify-content: space-between;
			}
		}

		&-item:nth-child(even) {
			// margin-left:
		}
	}
</style>
