import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
	state: {
		count: 0,
		globalData: {
			comment: 'h5-client',
			desc: 'vue+vue-cli+vue-router+axios+vuex+vant h5商场'
		},
		defaultAddress: {},
		userInfo: {
			avatarUrl: '',
			nick: '',
			province: '',
			city: '',
			mobile: ''
		}
	},
	mutations: {
		increment(state) {
			state.count++
		},
		updateDefaultAddress(state, address) {
			state.defaultAddress = address
		},
		updateUserInfo(state, userInfo) {
			state.userInfo = Object.assign(state.userInfo, userInfo)
		}
	},
	actions: {},
	modules: {}
})
export default store