<template>
	<div class="cart-container">
		<div class="header">
			<div class="header-top">
				<div class="header-title">
					购物车({{goods.length}})
				</div>
				<div class="header-total">
					共{{goods.length}}件宝贝
				</div>
			</div>
			<div class="header-manage" @click="toDelete">
				{{headerManage}}
			</div>
		</div>
		<van-empty description="空空乳液" v-if="!goods.length">
		  <van-button round type="danger" class="bottom-button" to="/home">
		    没有商品？去看看
		  </van-button>
		</van-empty>
		<div class="main" v-if="goods.length">
			<div class="group balance-group">
				<van-cell value="〇米商场" />
				<van-checkbox-group class="list" v-model="checkedGoods">
					<van-checkbox class="list-item" v-for="(item, index) in goods" :key="index" :name="item.id" checked-color="#ff4444"
					 label-disabled>
						<van-card :title="item.name" :desc="item.propTitle" :price="item.price" :thumb="item.pic">
							<div slot="num">
								<van-stepper v-model="item.selectedNum" @change="onNumberChange" />
							</div>
						</van-card>
					</van-checkbox>
				</van-checkbox-group>
			</div>
		</div>
		<van-submit-bar v-if="!isDelete" :disabled="!checkedGoods.length" :button-text="submitBarText" text-align="left" @submit="onSubmit">
			<van-checkbox v-model="checkedAll" checked-color="#ff4444" @click="checkedAllGoods">全选</van-checkbox>
			<div class="buyprice">
				<p class="p1">合计</p>
				<p class="p2">
					¥{{ totalPrice }}
				</p>
			</div>
		</van-submit-bar>
		<van-submit-bar v-if="isDelete" :disabled="!checkedGoods.length" button-text="删除" text-align="left" @submit="onDeleteClicked">
			<van-checkbox v-model="checkedAll" checked-color="#ff4444" @click="checkedAllGoods">全选</van-checkbox>
		</van-submit-bar>
		<!-- 底部栏开始 -->
		<van-tabbar route v-model="active" active-color="#ff6700">
			<van-tabbar-item replace to="/home" icon="home-o">
				首页
			</van-tabbar-item>
			<van-tabbar-item replace to="/cart" icon="cart-o">
				购物车
			</van-tabbar-item>
			<van-tabbar-item replace to="/user" icon="manager-o">
				我的
			</van-tabbar-item>
		</van-tabbar>
		<!-- 底部栏结束s -->
	</div>
</template>

<script>
	import util from '@/common/util/util'
	export default {
		data() {
			return {
				active: 1,
				length: 68,
				money: 0,
				checked: false,
				checkedAll: false,
				isDelete:false,
				headerManage:'管理',
				totalPrice: 0,
				checkedGoods: [],
				goodsId: [],
				list: [],
				title: '',
				isleftarrow: '',
				isCart: ''
			}
		},
		components: {

		},
		computed: {
			submitBarText() {
				const count = this.checkedGoods.length;
				return '结算' + (count ? `(${count})` : '')
			},
			// totalPrice() {
			// 	const price = this.goods.reduce((total, item) => total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price *
			// 		item.selectedNum : 0), 0);
			// 	return parseFloat(price.toFixed(2));
			// },
		},
		filters: {
			formatPoint(val) {
				return parseFloat((val * 100).toFixed(2));
			}
		},
		watch: {
			checkedAll() {
				// this.checkedAll ? this.checkedGoods = this.goods.map(item => item.id) : this.checkedGoods = [];
			},
			checkedGoods() {
				this.count()
				//如果选择的商品数与购物车总数相等，全选为选中
				if (this.checkedGoods.length === this.goods.length) {
					this.checkedAll = true;
				} else {
					this.checkedAll = false;
				}
			}
		},
		created() {
			this.getCart();
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
			this.isCart = this.$route.meta.isCart;
		},
		methods: {
			getCart() {
				this.goods = util.storage.get('cartInfo') || [];
				console.log(this.goods);
			},
			checkedAllGoods() {
				this.checkedAll ? this.checkedGoods = this.goods.map(item => item.id) : this.checkedGoods = [];
			},
			onNumberChange(value) {
				console.log(value);
				util.storage.set('cartInfo', this.goods);
				this.count();
			},
			count() {
				const price = this.goods.reduce((total, item) => total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price *
					item.selectedNum : 0), 0);
				this.totalPrice = parseFloat(price.toFixed(2));
			},
			toDelete() {
				this.isDelete = !this.isDelete;
				this.headerManage = this.isDelete ? '删除':'管理';
				console.log(this.isDelete);
			},
			onDeleteClicked(){
				this.$dialog.confirm({
					message: `确定要删除这${this.checkedGoods.length}个商品吗?`
				}).then(() => {
					this.goods = this.goods.filter(item => !(this.checkedGoods.indexOf(item.id) !== -1));
					this.checkedGoods = [];
					
					util.storage.set('cartInfo', this.goods)
				})
			},
			onSubmit() {
			  util.storage.set('cartInfo', this.goods)
			  this.$router.push({ path: '/order-submit', query: { type: 'cart', checkedGoods: this.checkedGoods }})
			}
		}
	}
</script>

